/**
 * 
 * @param password
 * @param new_pwd
 * @param confirm_pwd
 */
export function changeMyPassword(password, new_pwd, confirm_pwd) {
  const data = Qs.stringify({
    new_pwd: new_pwd,
    password: password,
    confirm_pwd: confirm_pwd
  })
  return request({
    url: '/auth/new-change-pwd/',
    method: 'post',
    data
  })
}

