const saveState = {
  //  -- 
  state: {
    isSave: true, // 
    newMessage: '',
  },

  mutations: {
    setIsSave(state, data) {
      state.isSave = data
    },

    SET_MESSAGE_NEW_TIPS: (state, newMessage) => {
      state.newMessage = newMessage
    },
  }
}

export default saveState




